import React, { useContext } from "react";
import { Link as GatsbyLink } from "gatsby";
import { Box, Text, Stack, Heading } from '@chakra-ui/react';

import Icon from "../../../images/icon.png"
import Drawer from "../../components/Drawer";
import IconButton from "../../components/IconButton";
import Hamburger from "../../../icons/hamburger";

import { Badge } from '@chakra-ui/react'

import { useDisclosureWithAnimation } from "../../../hooks/useDisclosure";

import cartContext from "../../../context/cartContext";

import "./styles.scss";

const CartItemsPill = () => {
    const { cart } = useContext(cartContext);

    const totalItems = cart.reduce((total, item) => {
        return total + item.quantity;
    }, 0);

    if (totalItems === 0) {
        return null;
    }

    return (
        <Badge  ml='2' fontSize='0.8em' colorScheme='pink'>
            {totalItems}
        </Badge>
    );
}

const DesktopNavBar = ({ leftHandLinks, rightHandLinks }) => {
    return (
        <div className="DesktopNavigation">
            <img className="BirdIcon" src={Icon} />
            <nav className="NavBar">
                <ul>
                    {leftHandLinks.map((l, idx) => {
                        return (
                            <li key={idx}>
                                <GatsbyLink
                                    color='black.500'
                                    activeStyle={l.isActive ? { color: "#ffb6be", fontWeight: 'bold' } : {}}
                                    to={l.href}>
                                    <Heading as='h3' size='sm' fontSize='16px'>{l.text}</Heading>
                                </GatsbyLink>
                            </li>
                        );
                    })}
                </ul>
                <ul className="RightHandLinks">
                    {rightHandLinks.map((l, idx) => {
                        return (
                            <li key={idx}>
                                <GatsbyLink
                                    color='black.500'
                                    activeStyle={l.isActive ? { color: "#ffb6be", fontWeight: 'bold' } : {}}
                                    to={l.href}>
                                    {l.text}
                                    {l.href === "/cart" &&
                                        <CartItemsPill />
                                    }
                                </GatsbyLink>
                            </li>
                        );
                    })}
                </ul>
            </nav>
        </div>
    );
};

export const NavBar = () => {
    const leftHandLinks = [
        {
            href: "/",
            text: "Home",
        }, 
    ];
    const rightHandLinks = [
        // {
        //     href: "/cart",
        //     text: "Cart",
        // },
    ];

    const {
        state,
        onOpen,
        onClose,
    } = useDisclosureWithAnimation();

    return (
        <nav className="ConsolidatedNavBar">
            <DesktopNavBar leftHandLinks={leftHandLinks} rightHandLinks={rightHandLinks} />

            <IconButton
                className={"NavBarIcon"}
                colorScheme='black'
                onClick={onOpen}
                ariaLabel='Open Navigation'>
                <Hamburger iconColor={'black'} />
            </IconButton>
            <Drawer
                state={state}
                onClose={onClose}
            >
                <ul className="MobileNavBar">
                    {[...leftHandLinks, ...rightHandLinks].map((l, idx) => {
                        return (
                            <li key={idx}>
                                <GatsbyLink
                                    color='black.500'
                                    activeStyle={l.isActive ? { color: "#ffb6be", fontWeight: 'bold' } : {}}
                                    onClick={() => {
                                        onClose();
                                    }}
                                    to={l.href}>
                                    {l.text}
                                    {l.href === "/cart" &&
                                        <CartItemsPill />
                                    }
                                </GatsbyLink>
                            </li>
                        );
                    })}
                </ul>
            </Drawer>
       </nav>
    );
};

export default NavBar;