import React from "react";

const siteName = "Crochet Nest";

export function CommonHead(props) {
    const description = props?.data?.page?.description || 
        "Discover handmade crochet bird gifts that bring smiles and comfort to you and your loved ones.";
    const title = props.data.page ? props.data.page.name : "The Crochet Nest";
    const image = props.data.page.image ? props.data.page.image : `https://crochetnest.com/icons/icon-512x512.png?v=f3b8fbdf6559bf7b0b72f12e0c989bab`;
    const url = props.canonicalUrl || `https://crochetnest.com${props.location.pathname}`;
    return (
        <>
            <meta name="theme-color" content="#ffdbdf" />
            <title>{title}</title>

            <link 
                rel="canonical" 
                href={url}
                data-baseprotocol="https:"
                data-basehost="crochetnest.com" />

            <meta name="description" content={description} />

            <meta name="twitter:card" content="summary"></meta>
            <meta name="twitter:description" content={description}></meta>
            <meta name="twitter:title" content={title}></meta>
            <meta name="twitter:image" content={image}></meta>
            <meta name="twitter:site" content={siteName}></meta>
            <meta name="twitter:creator" content={siteName}></meta>

            <meta property="og:url" content={url}></meta>
            <meta property="og:type" content="website"></meta>
            <meta property="og:site_name" content={siteName}></meta>
            <meta property="og:title" content={title}></meta>
            <meta property="og:description" content={description}></meta>
            <meta property="og:image" content={image}></meta>
        </>
    )
}

export default CommonHead;