import React from "react";
import PageFooter from "./components/Footer";
import Menu from "./menu";
import {
  ChakraProvider,
  extendTheme, defineStyle, defineStyleConfig
} from '@chakra-ui/react'

import cartContext from "../context/cartContext";

import { Box } from "@chakra-ui/react";

import "./styles.scss";

const nonBoldHeading = defineStyle({
    fontWeight: "300"
})

const Heading = defineStyleConfig({
  variants: {
    nonBoldHeading: nonBoldHeading,
  }
});

const Button = defineStyleConfig({
  baseStyle: {},
  sizes: {},
  variants: {
    white: defineStyle({
      border: '2px solid',
      borderColor: 'white',
      color: 'white',
    }),
    pink: defineStyle({
      color: 'white',
      backgroundColor: 'rgb(240, 142, 128)',
      _hover:{
        background: "RGB(255, 197, 172)",
      }
    }),
  },
  defaultProps: {},
});

const Select = defineStyleConfig({
  field: {
    background: 'white',
  },
  option: {
    background: 'white',
  },
});

const Link = defineStyleConfig({
  baseStyle: {},
  sizes: {},
  variants: {
    white: defineStyle({
      border: '2px solid',
      borderColor: 'white.500',
      color: 'white.500',
    }),
    purple: defineStyle({
      border: '2px solid',
      borderColor: 'purple.500',
      color: 'purple.500',
    }),
  },
  defaultProps: {},
});

const theme = extendTheme({
  components: {
    Select,
    Heading,
    Button,
    Link
  },
});

const useCart = () => {
  const [cart, setCart] = React.useState([]);

  React.useEffect(() => {
    const newCart = sessionStorage.getItem('cart');
    if (newCart) {
      setCart(JSON.parse(newCart));
    }
  }, []);

  const updateQuantity = (priceId, id, quantity) => {
    const newCart = cart.filter(item => item.priceId !== priceId);
    if (quantity > 0) {
      newCart.push({
        priceId,
        id,
        quantity,
      });
    }
    const sortedCart = newCart.sort((a, b) => {
      if ( a.id < b.id ){
        return -1;
      }
      if ( a.id > b.id ){
        return 1;
      }
      return 0;
    });

    sessionStorage.setItem('cart', JSON.stringify(sortedCart));
    setCart(sortedCart);
  };

  const wipeCart = () => {
    sessionStorage.setItem('cart', JSON.stringify([]));
    setCart([]);
  };

  return {
    cart,
    updateQuantity,
    wipeCart,
  }
};

export const MainSectionWrapper = (props) => {
  const {cart, wipeCart, updateQuantity} = useCart();

  return (
    <ChakraProvider theme={theme}>
      <cartContext.Provider value={{ cart, wipeCart, updateQuantity }}>
        <Box>
          <main className="MainSection">
            <Menu />

            <div className="MainSectionInner">
              {props.children}
            </div>
          </main>
          <PageFooter />
        </Box>
      </cartContext.Provider>
    </ChakraProvider>
  );
}


export default MainSectionWrapper 