import React from "react";

import IconButton from "../IconButton";
import CloseIcon from "../../../icons/close";
import { STATE } from "../../../hooks/useDisclosure";

import "./styles.scss";

const DrawerOverlay = () => {
    return (
        <div className="DrawerOverlay" />
    );
}

const DrawerContent = (props) => {
    const {state} = props;
    const drawerContent = React.useRef(null);

    const width = drawerContent?.current?.offsetWidth || 380;
    const marginStyle = {
        marginLeft: state === STATE.CLOSED || state === STATE.CLOSING ? `-${width}px` : `0` 
    };
    return (
        <div ref={drawerContent} className="DrawerContent" style={marginStyle}>
            <div className="CloseButtonContainer">
                <IconButton onClick={props.onClose} >
                    <CloseIcon />
                </IconButton>
            </div>
            {props.children}
        </div>
    );
}

export const Drawer = (props) => {
    const {state, onClose} = props;

    React.useEffect(() => {
        if (state !== STATE.CLOSED) {
            document.querySelector('body').classList.add('PreventScroll');
        }

        return () => {
            document.querySelector('body').classList.remove('PreventScroll');
        };
    }, [state]);

    const style = { visibility: state === STATE.CLOSED ? 'hidden' : 'visible' };

    return (
        <div className="Drawer" style={style}>
            <DrawerOverlay />
            <DrawerContent onClose={onClose} state={state}>
                {props.children}
            </DrawerContent>
        </div>
    );
};

export default Drawer;