import * as React from "react";

import {cn, cnV2} from "../../../classnames";

import { Heading } from "@chakra-ui/react";

import "./styles.scss";

const header = (type) => {
    return (props) => {
        const {center} = props;

        switch (type) {
            case 'h1':
                return <Heading as='h1' {...props}>{props.children}</Heading>
            case 'h2':
                return <Heading as='h2' {...props}>{props.children}</Heading>
            case 'h3':
                return <Heading as='h3' {...props}>{props.children}</Heading>
            case 'h4':
                return <Heading as='h4' {...props}>{props.children}</Heading>
            case 'h5':
                return <Heading as='h5' {...props}>{props.children}</Heading>
            case 'h6':
                return <Heading as='h6' {...props}>{props.children}</Heading>
            default:
                return <Heading as='p' {...props}>{props.children}</Heading>
        }
    };
};

export const H1 = header('h1');
export const H2 = header('h2');
export const H3 = header('h3');
export const H4 = header('h4');
export const H5 = header('h5');
export const H6 = header('h6');